import { TrackballControls } from 'three/addons/controls/TrackballControls.js';
import { CSS3DRenderer, CSS3DObject } from 'three/addons/renderers/CSS3DRenderer.js';

const table = [
    'Ashy Drongo', 'AshyDrongo', '', "Common",1,
    'Asian Koel', 'AsianKoel', '', "Rare",1,
    'Barn Owl', 'BarnOwl', ' ', "Very Rare",1,
    'Black Kite', 'BlackKite', '', "Very Common",1,
    'Blue Rock Pigeon', 'BlueRockPigeon', '', "Very Common",1,
    'Cinereous Tit', 'CinereousTit', ' ', "Rare",1,
    'Common Sparrow', 'CommonSparrow', '', "Rare",1,
    'Common Tailorbird', 'CommonTailorbird', '', "Rare",1,
    'Coppersmith Barbet', 'CoppersmithBarbet', '', "Common",1,
    'Dusky Crag Martin', 'DuskyCragMartin', '', "Common",1,
    'Golden Oriole', 'GoldenOriole', '', "Common",1,
    'Green Bee Eater', 'GreenBeeEater', ' ', "Common",1,
    'House Crow', 'HouseCrow', '', "Very Common",1,
    'Indian Grey Hornbill', 'IndianGreyHornbill', '', "Rare",1,
    'Indian White Eye', 'IndianWhiteEye', '', "Rare",1,
    'Jungle Crow', 'JungleCrow', '', "Very Common",1,
    'Purple Rumped Sunbird', 'PurpleRumpedSunbird', ' ', "Common",1,
    'Purple Sunbird', 'PurpleSunbird', ' ', "Common",1,
    'Red Naped Ibis', 'RedNapedIbis', ' ', "Rare",1,
    'Red Vented Bulbul', 'RedVentedBulbul', ' ', "Common",1,
    'Red Whiskered Bulbul', 'RedWhiskeredBulbul', ' ', "Common",1,
    'Rose Ringed Parakeet', 'RoseRingedParakeet', ' ', "Common",1,
    'White Spotted Fantail', 'WhiteSpottedFantail', '', "Rare",1,

];


let camera, scene, renderer, controls;
let geometry, material, mesh;

let sphereRadius = 600;

const objects = [];
const targets = { table: [], sphere: [], helix: [], grid: [] };


let onclicklisteneradded = 0;

init();
animate();
