import { useCallback } from 'react';
import './../App.css';
import { mount } from './Cube';

function Model_React() {
  const containerRef = useCallback(mount, []);
  let image = require("../Files/" + "BlackKite.jpg");
  return (
    <div style={{backgroundColor: "#000000"}}>
      <center>
        <div id='floatingImageDiv' style={{maxWidth: "70vw", maxHeight: "90vh", width: "auto", left: "0", right: "0", top: "20vh", background: "linear-gradient(180deg, rgba(32,73,91,0.7) 0%, rgba(22,90,134,0.756827731092437) 100%)", marginLeft: "auto", marginRight: "auto", position: "absolute", textAlign: "center", visibility: "hidden", zIndex: "1", display: "flex", borderRadius: "20px", justifyContent: "center"}}>
            <table>
                <tbody>
                    <tr>
                        <td>
                            <p id="floatingImageTitle" style={{color: "white", fontSize: "150%"}}>Title</p>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <img id="floatingImage" src={image} style={{borderRadius: "10px", maxHeight: "80vh", width: "65vw", height: "auto", objectFit: "contain", display: "block", marginTop: "20px", marginBottom: "20px"}}/>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div style={{width: "90vw", height: "80vh", top: "100px", zIndex: "1", overflow: "clip"}} id="container" className="Cube-container" ref={containerRef}></div>
      </center>
      
    </div>
  )
}

export default Model_React;