import React from "react";
import { List, ListItem, Segment } from "semantic-ui-react";
import 'semantic-ui-css/semantic.min.css'
import '../App.css';

export default(props) => {
    return(
        <div style={{marginTop:"100px", textAlign:"left"}}>
            <Segment inverted>
                <List inverted bulleted size="mini">
                    <ListItem><p style={{color: "#888888"}}>11/01/2023 : Juvenile Red-Naped Ibis seen trying flying. Flying at height of around 10-15 cm. Was injured and/or ill.</p></ListItem>
                    <ListItem><p style={{color: "#888888"}}>20/03/2023 : Red-naped Ibis seen on top of building.</p></ListItem>
                    <ListItem><p style={{color: "#888888"}}>20/10/2023 : Barn Owl on mango tree.</p></ListItem>
                    <ListItem><p style={{color: "#888888"}}>Mostly everyday, Indian Grey Hornbill seen in the morning near mango tree and surrounding</p></ListItem>
                    <ListItem><p style={{color: "#888888"}}>11/01/2024 : Saw a flock of seven Red-naped Ibis flying.</p></ListItem>
                    <ListItem><p style={{color: "#888888"}}>15/01/2024 : Mating season of Coppersmith Barbets has started. A pair can be seen chasing each other on the top of trees, especially the Pimpal Tree.</p></ListItem>
                    <ListItem><p style={{color: "#888888"}}>18/01/2024 : Indian White-Eye was giving a lot of calls. Ashy Drongo's favourite place is the Khota Badam tree and the dried tree near Sharad Arena, though it could be seen anywhere. Fantail was seen on the mango tree. Atleast two pairs of Coppersmith Barbets present on the Pimpal Tree. Most of the time they reside on that tree itself, chasing each other and eating fruits.</p></ListItem>
                </List>

                <List style={{marginTop:"60px"}} size="mini">
                    <ListItem>Feel free to contact me for more details, or for reporting more observations!</ListItem>
                </List>
            </Segment>
        </div>
    )
};